@mixin icon-shadow {
    text-shadow: 0 0 5px #00000080;
    -webkit-text-shadow: 0 0 5px #00000080;
    -moz-text-shadow: 0 0 5px #00000080;
    -ms-text-shadow: 0 0 5px #00000080;
    -o-text-shadow: 0 0 5px #00000080;

}

@mixin box-shadow {
    -webkit-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.25);
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.25);
}

.box-shadow {
    @include box-shadow;
}

body {
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-variation-settings: "slnt"0;
    background: var(--Lightest, #F5F3F1) !important;
}

* {
    font-family: 'Inter';
    font-weight: 400;
}

@supports (font-variation-settings: "wght"450) {
    * {
        font-family: 'Inter';
        font-weight: 450;
    }
}

.title-banner {
    @media screen and (max-width: 500px) {
        font-size: 18px !important;
    }
}

.header-icon-filter {
    font-size: 18px !important;
}

.input-terrazon {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    width: 100%;
    animation: fade-in 1s ease-in-out;
}

/* Animación de desvanecimiento */
@keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


.carousel-over-content {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    z-index: 9999;

    h1 {
        color: white;
        font-size: 48px;
        text-shadow: -1px -1px 1px rgba(255, 255, 255, .1), 1px 1px 1px rgba(0, 0, 0, .5), 2px 2px 9px #000000;
    }

    label {
        color: #231D46;
        size: 13px;
        font-weight: 600;
        padding-left: 10px;
    }

    .card {
        .item {
            padding: 14px;
        }

        select {
            border: none;
            font-size: 11px;
            color: #8F8F8F;
        }
    }

    .btn-primary {
        font-size: 14px;
        border-radius: 6px;
        padding: 5px 25px;
        background-color: #159E12 !important;
        border: none !important;
    }

    .item {
        position: relative;

        .item-content {
            margin-left: 14px;
        }
    }

    .item:nth-child(2),
    .item:nth-child(3),
    .item:nth-child(4) {
        &::before {
            position: absolute;
            left: 0;
            content: '';
            height: 60px;
            width: 1px;
            background-color: #C3C3C3;
        }
    }
}

