.carousel-container {
    position: relative;

    .car-nav {
        z-index: 999;
        top: 50%;
        position: absolute;
        cursor: pointer;
        @include icon-shadow;

        .icon-nav {
            font-size: 16px;
        }

        .fa-circle {
            color: $primary;
        }
    }

    .car-back {
        left: 0;
    }

    .car-next {
        @media screen and (min-width: 501px) {
            right: -10px;
        }

        @media screen and (max-width: 500px) {
            right: 40px;
        }
    }
}

.carousel-control-prev {
    justify-content: start !important;
    opacity: 1 !important;

}

.carousel-control-next {
    justify-content: end !important;
    opacity: 1 !important;
}

